  },
  methods: {
    init() {
      this.showEchartsData(); // 设备效率
      this.showEcharts2Data(); // 今日设备状态分布
      this.showEcharts3Data(); // 设备利用率
      this.showEcharts4Data(); // 设备当班稼动率
      this.getTodayElectricity(); // 用电统计
      this.getElectriRanking(); // 用电排名
      if (this.kanbanConfig.showEnergy) {
        this.showElectric(); // 电表数据
      }
    },
    // 看板时间
    async getDataTime() {
      try {
        const res = await getServerTime({}, {});
        this.dataTime = res.rows;
        this.toGetTime();
      } catch (error) {
        console.log(error);
      }
    },
    getTodayFirstDay() {
      const time = new Date(this.dataTime);
      var y = time.getFullYear(); // 获取年份
      var m = time.getMonth() + 1; // 获取月份
      var d = time.getDate(); // 获取当月最后一日
      m = m < 10 ? '0' + m : m; // 月份补 0
      d = d < 10 ? '0' + d : d; // 日数补 0

      return [y, m, d].join('-') + ' 00:00:00';
    },
    getTodayLastDay() {
      const time = new Date(this.dataTime);
      var y = time.getFullYear(); // 获取年份
      var m = time.getMonth() + 1; // 获取月份
      var d = time.getDate(); // 获取当月最后一日
      m = m < 10 ? '0' + m : m; // 月份补 0
      d = d < 10 ? '0' + d : d; // 日数补 0

      return [y, m, d].join('-') + ' 23:59:59';
    },
    getSearchToday(num) {
      var date = new Date(this.dataTime - (60 * 60 * 1000 * 24) * num);
      var y = date.getFullYear(); // 获取年份
      var m = date.getMonth() + 1; // 获取月份
      var d = date.getDate(); // 获取当月最后一日
      m = m < 10 ? '0' + m : m; // 月份补 0
      d = d < 10 ? '0' + d : d; // 日数补 0
      return [y, m, d].join('-');
    },
    // 获取分组树形结构
    async getWorkshopData() {
      try {
        const res = await getGroupTree({}, { secretKey: this.kanbanConfig.key });
        this.groupOptions = dataFormatter(res.rows || []);
      } catch (error) {
        console.log(error);
      }
    },
    // 切换产线
    changeWorkGroup(val) {
      this.groupId = val[val.length - 1];
      this.current = 1;
      this.isShowDeviceList = false;
      this.title = this.$refs.workGroup.getCheckedNodes()[0].label;
      this.devList = [];
      // this.htmlDevState([]);
      this.init();
      this.showLineAndDev();
      this.isShowWorkGroup = false;
    },
    changeDeviceDataType() {
      this.isShowDeviceList = false;
      this.isDeviceDataType = false;
      // this.htmlDevState([]);
      this.showLineAndDev();
    },
    // 获取所在班次的时间范围
    getCurrentShiftTimeRangeWithDate(shifts) {
      // 获取当前日期的年、月、日
      const now = new Date();
      const year = now.getFullYear();
      const month = now.getMonth() + 1;
      const day = now.getDate();

      // 获取当前时间的小时和分钟
      const currentHour = now.getHours();
      const currentMinute = now.getMinutes();
      // const currentSecond = now.getSeconds();

      // 计算当前时间的总分钟数
      const currentTimeInMinutes = currentHour * 60 + currentMinute;
      // 遍历班次对象，查找当前时间属于哪个班次
      for (const shift in shifts) {
        const [start, end] = shifts[shift].split('-').map(time => {
          const [hours, minutes] = time.split(':').map(Number);
          return hours * 60 + minutes;
        });
        if (end < start) {
          // 说明是跨天的班次
          if (currentTimeInMinutes >= start && currentTimeInMinutes <= 1440) {
            // return shifts[shift];
            console.log(shifts[shift]);
            const startTime = new Date(year, month - 1, day, Math.floor(start / 60), start % 60, 0);
            const endTime = new Date(year, month - 1, day, Math.floor(end / 60), end % 60, 0);
            endTime.setDate(day + 1);
            console.log(parseTime(startTime), parseTime(endTime));
            return [parseTime(startTime), parseTime(endTime)];
          }
        } else {
          if (start <= currentTimeInMinutes && currentTimeInMinutes <= end) {
            const startTime = new Date(year, month - 1, day, Math.floor(start / 60), start % 60, 0);
            const endTime = new Date(year, month - 1, day, Math.floor(end / 60), end % 60, 0);
            return [parseTime(startTime), parseTime(endTime)];
          }
        }
      }
      return [this.getTodayFirstDay(), this.getTodayLastDay()];
    },
    // 获取班次时间段
    getShiftTime() {
      return new Promise((resolve, reject) => {
        const condtion = {};
        if (this.getUrlKeyMain('cid', window.location.href)) {
          condtion.secretKey = this.kanbanConfig.key;
        }
        getConfigurationByEnum({ configurationTypeEnum: 'SHIFT_TIME' }, condtion).then(
          data => {
            if (data.code === '0000') {
              const shiftTime = data.rows.processConfiguration;
              if (shiftTime) {
                this.shiftConfig = true;
                const processConfiguration = JSON.parse(shiftTime);
                const [start, end] = this.getCurrentShiftTimeRangeWithDate(processConfiguration);
                this.stateStartTime = start;
                this.stateEndTime = end;
              } else {
                this.shiftConfig = false;
                this.stateStartTime = this.getTodayFirstDay();
                this.stateEndTime = this.getTodayLastDay();
              }
              console.log(this.stateStartTime, this.stateEndTime);
              resolve(data);
            }
          }).catch(err => {
          console.log(err);
          reject(err);
        });
      });
      // try {
      //   const condtion = {};
      //   if (this.getUrlKeyMain('cid', window.location.href)) {
      //     condtion.secretKey = this.kanbanConfig.key;
      //   }
      //   const data = await getConfigurationByEnum(
      //     {
      //       configurationTypeEnum: 'SHIFT_TIME'
      //     },
      //     condtion
      //   );
      //   if (data.code === '0000') {
      //     const shiftTime = data.processConfiguration;
      //     if (shiftTime) {
      //       const processConfiguration = JSON.parse(shiftTime);
      //       const [start, end] = this.getCurrentShiftTimeRangeWithDate(processConfiguration);
      //       this.stateStartTime = start;
      //       this.stateEndTime = end;
      //     } else {
      //       this.stateStartTime = this.getTodayFirstDay();
      //       this.stateEndTime = this.getTodayLastDay();
      //     }
      //     this.showLineAndDev();
      //   }
      // } catch (error) {
      //   // showErrorNotify(TITLE.NOTIFY, error);
      // } finally {
      // }
    },
    // 获取生产配置
    async getYieldConfig() {
      try {
        const condtion = {};
        if (this.getUrlKeyMain('cid', window.location.href)) {
          condtion.secretKey = this.kanbanConfig.key;
        }
        const data = await getConfigurationByEnum(
          {
            configurationTypeEnum: 'PRODUCTION_CONFIGURATION'
          },
          condtion
        );
        if (data.code === '0000') {
          this.yiledKey = data.rows?.processConfiguration || '产量';
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    finga(divData) {
      this.timer = setInterval(() => {
        // 元素自增距离顶部1像素
        divData.scrollTop += 1;
        // 判断元素是否滚动到底部(可视高度+距离顶部=整个高度)
        if (divData.clientHeight + divData.scrollTop === divData.scrollHeight) {
          // 重置table距离顶部距离
          clearInterval(this.timer);
          divData.scrollTop = 0;
          this.finga(divData);
          // setTimeout(() => {
          //   divData.scrollTop = 0;
          // }, 2000);
          // setTimeout(() => {
          //   this.finga(divData);
          // }, 4000);
        }
      }, 50); // 滚动速度
    },
    finga4(divData) {
      this.timer4 = setInterval(() => {
        // 元素自增距离顶部1像素
        divData.scrollTop += 1;
        // 判断元素是否滚动到底部(可视高度+距离顶部=整个高度)
        if (divData.clientHeight + divData.scrollTop === divData.scrollHeight) {
          // 重置table距离顶部距离
          clearInterval(this.timer4);
          setTimeout(() => {
            divData.scrollTop = 0;
          }, 2000);
          setTimeout(() => {
            this.finga4(divData);
          }, 4000);
        }
      }, 50); // 滚动速度
    },
    async showElectric() {
      try {
        var condtion = {
          current: 1,
          size: 99999999
        };
        if (this.getUrlKeyMain('cid', window.location.href)) {
          condtion.secretKey = this.kanbanConfig.key;
        }
        const res = await getEnergyTodayUse({ energyEnum: 'ELECTRICITY' }, condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.electricList = res.rows;
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }
      try {
        // eslint-disable-next-line no-redeclare
        var condtion = {};
        if (this.getUrlKeyMain('cid', window.location.href)) {
          condtion.secretKey = this.kanbanConfig.key;
        }
        const res = await getEnergyTodayAndWeek({}, condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.todayElecticeNum = res.rows.electricityStatistics.dayEnergyConsumption;
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }
    },
    async showEchartsData() {
      if (this.devList.length === 0 || this.devLbIndex === this.devList.length - 1) {
        try {
          var condtion = {};
          // console.log('7788', localStorage.getItem('companyId'));
          if (this.getUrlKeyMain('cid', window.location.href)) {
            condtion.secretKey = this.kanbanConfig.key;
          }
          if (this.groupId) {
            condtion.conditionJson1 = [
              {
                column: 'groupId',
                operator: 1,
                ptype: 'int',
                type: 'eq',
                value: this.groupId
              }
            ];
          }
          const res = await deviceRealLiveGetDeviceStateReportStatistics({
            current: 1,
            size: 99999,
            startTime: this.getTodayFirstDay(),
            endTime: this.getTodayLastDay()
          }, condtion
          );
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.devList = res.rows;
            this.devLbIndex = 0;
            this.getDeviceTimeRecentDayUtilization();
          } else {
            this.devLbIndex = 0;
            this.devList = [];
          }
        } catch (error) {
          console.log(error);
        }
      } else {
        this.devLbIndex++;
        this.getDeviceTimeRecentDayUtilization();
      }
    },
    async getDeviceTimeRecentDayUtilization() {
      try {
        var condtion = {
          conditionJson1: [
            {
              column: 'deviceCode',
              operator: 1,
              ptype: 'string',
              type: 'eq',
              value: this.devList[this.devLbIndex].deviceCode
            }
          ]
        };
        if (this.getUrlKeyMain('cid', window.location.href)) {
          condtion.secretKey = this.kanbanConfig.key;
        }
        // console.log('lalal', condtion);
        const res = await deviceReportGetDeviceDayUtilization({
          startTime: this.getSearchToday(7) + ' 00:00:00',
          endTime: this.getSearchToday(0) + ' 23:59:59'
        },
        condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // console.log('你好', res);
          var sbxl = [];
          var pjxl = [];
          var time = [];
          var all = [];
          var numAll = 0;
          for (var i = 0; i < res.rows.length; i++) {
            // pjxl.push(Number((data.dataset * 100).toFixed(2)));
            sbxl.push(Number((res.rows[i].value * 100).toFixed(2)));
            time.push(this.formatDateThree(res.rows[i].time));
            all.push(Number((res.rows[i].value * 100).toFixed(2)));
            numAll += res.rows[i].value;
          }
          var avg = ((numAll / all.length) * 100).toFixed(2);
          for (var x = 0; x < all.length; x++) {
            pjxl.push(Number(avg));
          }
          // console.log('大家好', pjxl);
          this.showEcharts(sbxl, pjxl, time, Math.floor(Math.min.apply(null, all) - 1), this.devList[this.devLbIndex].deviceName);
        } else {
          this.showEcharts([], [], '', 0, '');
        }
      } catch (error) {
        console.log(error);
      }
    },
    // 用电统计
    async getTodayElectricity() {
      var condtion = {
        conditionJson1: []
      };
      if (this.groupId) {
        condtion.conditionJson1 = [
          {
            column: 'groupId',
            operator: 1,
            ptype: 'int',
            type: 'eq',
            value: this.groupId
          }
        ];
      }
      if (this.getUrlKeyMain('cid', window.location.href)) {
        condtion.secretKey = this.kanbanConfig.key;
      }
      const res = await getTodayAndYesterdayElectricityRecord({}, condtion);
      if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
        const data = res.rows?.today || [];
        this.todayElectriData.xAxisData = data.map(item => item.time.substring(11, 16));
        this.todayElectriData.data = data.map(item => item.value);
      }
    },
    // 用电排名
    async getElectriRanking() {
      var condtion = {
        conditionJson1: []
      };
      if (this.groupId) {
        condtion.conditionJson1 = [
          {
            column: 'groupId',
            operator: 1,
            ptype: 'int',
            type: 'eq',
            value: this.groupId
          }
