    // 设备当班稼动率
    async showEcharts4Data() {
      var condtion = {};
      if (this.getUrlKeyMain('cid', window.location.href)) {
        condtion.secretKey = this.kanbanConfig.key;
      }
      if (this.groupId) {
        condtion.conditionJson1 = [
          {
            column: 'groupId',
            operator: 1,
            ptype: 'int',
            type: 'eq',
            value: this.groupId
          }
        ];
      }
      try {
        await this.getShiftTime();
        const params = {
          current: this.current,
          size: this.size,
          startTime: this.stateStartTime,
          endTime: this.stateEndTime
        };
        if (this.shiftConfig) {
          params.shiftStart = this.stateStartTime.substr(11, 5);
          params.shiftEnd = this.stateEndTime.substr(11, 5);
        }
        const res = await deviceRealLiveGetDeviceStateReportStatistics(params, condtion);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          if (this.current * this.size >= res.total) {
            this.current = 1;
          } else {
            this.current++;
          }
          const utilization = [];
          var name = [];
          for (var i = 0; i < res.rows.length; i++) {
            const run = res.rows[i].deviceLiveReportVo.runTimeSum;
            const free = res.rows[i].deviceLiveReportVo.idleTimeSum;
            const waran = res.rows[i].deviceLiveReportVo.warnTimeSum;
            const temp = run ? parseFloat(((run / (run + free + waran)) * 100).toFixed(2)) : 0;
            utilization.push(temp);
            name.push(res.rows[i].deviceName);
          }
          this.showEcharts4(utilization, name);
        } else {
          this.showEcharts4([], []);
        }
      } catch (error) {
        console.log(error);
      }
    },
