    htmlDevState(devJSON) {
      this.devStateHtml = '';

      var sbLine = 9;
      var cnum = 75;

      var canWeb = true;
      if (window.innerWidth <= 1024) {
        sbLine = 6;
        cnum = 24;
        canWeb = false;
      } else if (window.innerWidth <= 1280) {
        sbLine = 8;
        cnum = 32;
        canWeb = false;
      }

      const height = (this.$refs.right1content.offsetHeight * 0.3) - cnum;

      var arr = devJSON;
      var html = '';

      for (var i = 0; i < arr.length; i++) {
        // 标记设备需要遍历几次
        var listNum = Math.ceil(arr[i].devListData.length / sbLine);
        // 标记第几次遍历
        var flag = 0;
        // 标记设备遍历长度
        var arrlength = 0;

        if (i !== 0) {
          var num = Math.ceil(arr[i - 1].devListData.length / sbLine);

          if (num >= 2) {
            if (canWeb) {
              html += '<div class="lineCON" style="margin-top:' + ((num * 1.1) * height) + 'px;" >';
            } else {
              html += '<div class="lineCON" style="margin-top:' + ((num - 1) * height) + 'px;" >';
            }
            // html += '<div class="lineCON" style="margin-top:' + ((num * 1.1) * height) + 'px;" >';
          } else {
            html += '<div class="lineCON" >';
          }
        } else {
          html += '<div class="lineCON" >';
        }

        html += '      <p class="lineName" >' + arr[i].lineName + '</p>';

        for (var j = 0; j < listNum; j++) {
          // 若设备长度与预设长度一致
          if (arr[i].devList <= sbLine) {
            arrlength += arr[i].devListData.length;
          } else {
            // 若下一次轮播设备数已经短于预设值
            if ((flag + 1) * sbLine > arr[i].devListData.length) {
              arrlength += (arr[i].devListData.length % sbLine);
            } else {
              arrlength += sbLine;
            }
          }
          flag++;
          html += '      <div class="lineDev" >';
          html += '        <div class="blackDev" >';

          for (var w = 0; w < sbLine; w++) {
            html += '       <div class="blackTask" ></div>';
          }
          html += '           <div class="blackLine" ></div>';
          html += '           </div>';
          html += '           <div class="blackState" >';
          for (var x = j * sbLine; x < arrlength; x++) {
            html += '           <div class="devTask state' + arr[i].devListData[x].deviceRealState + '" >';
            if (arr[i].devListData[x].isProductionCount === 1) {
              if (this.deviceDataType === '稼动率') {
                html += '          <span class="devTask_span2"  >' + (arr[i].devListData[x].timeActivation ? (arr[i].devListData[x].timeActivation + '%') : '-') + '</span>';
              } else {
                if (arr[i].devListData[x].currentShiftName) {
                  html += '          <span class="devTask_span2"  >' + arr[i].devListData[x].currentValue + '</span>';
                } else {
                  html += '          <span class="devTask_span2"  >' + arr[i].devListData[x].todayValue + '</span>';
                }
              }
            } else {
              html += '          <span class="devTask_span2"  ></span>';
            }
            html += '          <p>' + arr[i].devListData[x].deviceName + '</p>';
            html += '           </div>';
          }
          html += '          </div>';
          html += '      </div>';
        }
        html += '</div>';
      }
      this.devStateHtml = html;
      // $(".right1content").html(html);
      // setTimeout(() => {
      //   this.scrollDev();
      // }, 2500);
    },
    scrollDev() {
      //     num += 1;
      //     this.$refs.right1content.scrollTo(0, num);
      //     console.log('滚动高度', this.$refs.right1content.scrollTop);
      // var num = 0;
      // setInterval(() => {
      //   this.$nextTick(() => {
      //     num += 1;
      //     this.$refs.right1content.scrollTo(0, num);
      //     console.log('滚动高度', this.$refs.right1content.scrollTop);
      //   });
      // }, 50);

      var that = this;

      var newTop;
      // var up = this.$refs.right1content.scrollTop;
      // 使用定时器
      this.timer = setInterval(() => {
        this.$nextTick(() => {
          // console.log(this.$refs.right1content.scrollTop === newTop);
          // console.log(this.$refs.right1content.scrollTop, newTop);
          if (this.$refs.right1content.scrollTop === newTop) {
            // 清除定时器

            clearInterval(this.timer);
            setTimeout(() => {
              this.showLineAndDev();
            }, 2500);
          } else {
            // 每次在原来的基础上移动
            newTop = that.$refs.right1content.scrollTop;
            this.$refs.right1content.scrollTo(0, newTop + 1);
          }
        });
      }, 50);
    },
