<template>
  <div
    class="lineChart"
    ref="chart"
    :style="{ width: width, height: height }"
  ></div>
</template>
<script>
import { fontSize } from '@/utils/common';
const areaColor = {
  '#09DBA0': ['rgba(9, 219, 160, 0.8)', 'rgba(9, 219, 160, 0.5)', 'rgba(9, 219, 160, 0.1)'],
  '#F7CF5E': ['rgba(247, 207, 94, 0.8)', 'rgba(247, 207 ,94, 0.5)', 'rgba(247, 207, 94, 0.1)'],
  '#FF9745': ['rgba(255, 151, 69, 0.8)', 'rgba(255, 151, 69, 0.5)', 'rgba(255, 151, 69, 0.1)'],
  '#AA9FFF': ['rgba(170, 159, 255, 0.8)', 'rgba(170, 159, 255, 0.5)', 'rgba(170, 159, 255, 0.1)'],
  '#72BCFF': ['rgba(114, 188, 255, 0.8)', 'rgba(114, 188, 255, 0.5)', 'rgba(114, 188, 255, 0.1)']
};
export default {
  name: 'LineChart',
  props: {
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '100%'
    },
    lineColor: {
      type: String,
      default: '#09DBA0'
    },
    chartData: {
      type: Object,
      default: () => ({})
    }
  },

  data() {
    return {
      data: [],
      loading: false,
      chart: null
    };
  },
  watch: {
    chartData: {
      handler() {
        this.drawChart();
      },
      deep: true
    }
  },
  methods: {
    drawChart() {
      const option = {
        grid: {
          left: '5%',
          right: '12%',
          bottom: '7%',
          top: '14%',
          containLabel: true
        },
        color: this.lineColor,
        tooltip: {
          show: true,
          trigger: 'axis'
        },
        xAxis: {
          name: '',
          type: 'category',
          boundaryGap: false,
          data: this.chartData.xAxisData,
          axisLabel: {
            fontSize: fontSize(16)
          },
          axisTick: {
            show: false
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: '#2077A3'
            }
          },
          nameTextStyle: {
            fontSize: fontSize(16)
          }
        },
        yAxis: {
          name: 'KW.h',
          type: 'value',
          axisLine: {
            show: true,
            lineStyle: {
              color: '#2077A3'
            }
          },
          axisTick: {
            show: false
          },
          splitLine: {
            show: false
          },
          axisLabel: {
            fontSize: fontSize(16)
          },
          nameTextStyle: {
            fontSize: fontSize(16)
          }
        },
        textStyle: {
          // color: '#929ee6'
          color: '#CACACA'
        },
        series: [
          {
            name: '用电',
            type: 'line',
            zlevel: 2,
            // smooth: true,
            lineStyle: {
              width: 2,
              color: this.lineColor
            },
            // label: {
            //   show: true,
            //   position: 'top'
            // },
            showSymbol: true,
            areaStyle: {
              // opacity: 0.7,
              color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  // color: '#8FFCA0'
                  color: areaColor[this.lineColor][0]
                },
                {
                  offset: 0.25,
                  color: areaColor[this.lineColor][1]
                  // color: "rgba(31,176,255,1)",
                },
                {
                  offset: 1,
                  // color: '#0BCBFB'
                  color: areaColor[this.lineColor][2]
                }
              ])
            },
            label: {
              show: true,
              position: 'top',
              formatter: (data) => {
                if (data.dataIndex % 2 === 0) {
                  return '';
                } else {
                  return data.value;
                }
              }
            },
            emphasis: {
              focus: 'series'
            },
            data: this.chartData.data
          }
        ]
      };
      this.chart.setOption(option);
    },
    init() {}
  },
  mounted() {
    this.chart = this.$echarts.init(this.$refs.chart);
    setTimeout(() => {
      this.drawChart();
    }, 500);
  }
};
</script>
<style lang="less" scoped></style>
