-->
<template>
  <div class="productTask wh100" ref="taskList">
    <state-label class="state_label"></state-label>
    <vue-seamless-scroll :data="list" :class-option="optionHover" class="seamless-warp">
      <ul class="lineWra" ref="lineWra">
        <li class="itemLine" v-for="line in list" :key="line[0][0].deviceId">
          <p class="lineName">{{ line[0][0].lineName }}</p>
          <div class="lineCon" v-for="row in line" :key="row[0].deviceId">
            <div class="bgLine"></div>
            <div class="itemDevice" v-for="item in row" :key="item.deviceId">
              <template v-if="JSON.stringify(item) !== '{}'">
                <div :class="['wh100', 'innerState', 'flex', 'state' + item.deviceRealState]">
                  <span class="todayValue" v-if="deviceDataType === '稼动率'">{{ item.timeActivation ? (item.timeActivation + '%') : '-' }}</span>
                  <span class="todayValue" v-else>{{ item.isProductionDevice === 1 ? item.currentValue : '' }}</span>
                  <div class="deviceName">{{ item.deviceName }}</div>
                </div>
              </template>
            </div>
          </div>
        </li>
      </ul>
    </vue-seamless-scroll>
  </div>
</template>

<script>
import vueSeamlessScroll from 'vue-seamless-scroll';
import StateLabel from '@/components/common/kanban/StateLabel';
export default {
  name: 'ProductTask',
  components: {
    StateLabel,
    vueSeamlessScroll
  },
  data() {
    return {
      limitMoveNum: 4,
      isShow: false
    };
  },
  props: {
    list: {
      type: Array,
      require: true
    },
    deviceDataType: {
      type: String,
      default: '产量'
    }
  },
  computed: {
    optionHover() {
      return {
        step: 0.4, // 数值越大速度滚动越快
        limitMoveNum: this.limitMoveNum, // 开始无缝滚动的数据量 this.list.length
        hoverStop: true, // 是否开启鼠标悬停stop
        direction: 1, // 0向下 1向上 2向左 3向右
        openWatch: true, // 开启数据实时监控刷新dom
        singleHeight: 0, // 单步运动停止的高度(默认值0是无缝不停止的滚动) direction => 0/1
        singleWidth: 0, // 单步运动停止的宽度(默认值0是无缝不停止的滚动) direction => 2/3
        waitTime: 1000 // 单步运动停止的时间(默认值1000ms)
      };
    }
  },
  watch: {
    list: {
      handler(val) {
        this.$nextTick(() => {
          console.log(this.$refs.lineWra.clientHeight > this.$refs.taskList.clientHeight);
          if (this.$refs.lineWra.clientHeight > this.$refs.taskList.clientHeight) {
            this.limitMoveNum = 1;
          } else {
            // this.limitMoveNum = this.totalNum() + 1;
            this.limitMoveNum = 99999;
          }
        });
      },
      // immediate: true,
      deep: true
    }
  },
  methods: {
    totalNum() {
      let num = 0;
      this.list.forEach(item => {
        num += item.length;
      });
      return num;
    }
    // limitMoveNum() {
    //   let num = 0;
    //   this.list.forEach(item => {
    //     num += item.length;
    //   });
    //   return num > 2 ? 19 : 3;
    // }
  },
  mounted() {
    // console.log(this.$refs.taskList.clientWidth)
    // console.log(window.getComputedStyle(this.$refs.taskList).width)
  }
};
</script>

<style lang="less" scoped>
.wh100 {
  width: 100%;
  height: 100%;
}

ul,
li,
p {
  list-style: none;
  padding: 0;
  margin: 0;
}

.flex {
  display: flex;
  align-items: center;
  justify-content: center;
}

.productTask {
  position: relative;
  padding: 10px 30px;
  color: #FFFFFF;

  .state_label {
    position: absolute;
    right: 3%;
    top: -4%;
  }

  .seamless-warp {
    height: 100%;
    overflow: hidden;
  }

  .lineWra {
    // overflow: auto;
    .lineName {
      color: #4bf3f9;
      font-size: 2.2rem;
      margin-top: 8px;
      margin-bottom: 0.5rem;
    }
    .itemLine {
      margin-bottom: 1rem;

      .lineCon {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-around;
        width: 100%;
        height: 95px;
        background: rgba(14, 85, 244, 0.25);
        border-left: 8px solid rgba(6, 106, 255, 0.4);
        border-right: 8px solid rgba(6, 106, 255, 0.4);
        .bgLine {
          position: absolute;
          z-index: 0;
          top: 50%;
          transform: translateY(-0.5rem);
          width: 100%;
          height: 0.5rem;
          background-color: #021e5c;
        }
        .itemDevice {
          position: relative;
          width: 9%;
          // width: 12rem;
          height: 34px;
          background-color: #021e5c;
          .innerState {
            position: relative;
            // position: absolute;
            z-index: 2;
            .todayValue {
              color: #051580;
              font-size: 2.2rem;
              font-family: Vox-Regular2;
            }
            .deviceName {
              position: absolute;
              top: 110%;
              // bottom: -2.5rem;
              width: 100%;
              font-size: 1.5rem;
              text-align: center;
              line-height: 100%;
              // white-space: pre-wrap;
              // white-space: nowrap;
              // text-overflow: ellipsis;
              // overflow: hidden;
            }
          }
        }
        .state1 {
          background-color: #09dba0;
        }
        .state2 {
          background-color: #e2cd16;
        }
        .state3 {
          background-color: #a0a0a0;
        }
        .state4 {
          background-color: #f1464e;
        }
      }
    }
  }
}

@media screen and (max-width: 1280px) {
  .productTask {
    padding: 8px 25px;

    .lineWra {
      .lineName {
        font-size: 16px;
        margin-top: 12px;
        margin-bottom: 4px;
      }
      .itemLine {
        margin-bottom: 1rem;

        .lineCon {
          height: 85px;
          .bgLine {
            height: 5px;
          }
          .itemDevice {
            width: 10%;
            height: 32px;
            .innerState {
              .todayValue {
                font-size: 18px;
              }
              .deviceName {
                font-size: 12px;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1024px) {
  .productTask {
    padding: 8px 25px;

    .lineWra {
      .lineName {
        font-size: 16px;
        margin-top: 10px;
        margin-bottom: 4px;
      }
      .itemLine {
        margin-bottom: 1rem;

        .lineCon {
          height: 80px;

          .bgLine {
            height: 5px;
          }
          .itemDevice {
            width: 13%;
            height: 30px;
            .innerState {
              .todayValue {
                font-size: 17px;
              }
              .deviceName {
                // bottom: -36px;
                font-size: 12px;
              }
            }
          }
        }
      }
    }
  }
}
</style>
